<template>
  <v-dialog v-model="dialog" persistent max-width="300px">
    <template v-slot:activator="{ on, attrs }">
      <v-tooltip bottom v-model="isShow" :activator="$refs.myIcon">
        <span>{{tipText}}</span>
      </v-tooltip>
      <v-icon
        ref="myIcon"
        class="mr-2"
        v-on="on"
        v-bind="attrs"
        @mouseover="isShow = !isShow"
        @mouseleave="isShow = !isShow"
      >{{icon}}</v-icon>
    </template>

    <v-card>
      <v-card-title>
        {{text}}
        <v-spacer />
        <v-icon aria-label="Close" @click="dialog = false">mdi-close</v-icon>
      </v-card-title>

      <v-card-text class="pb-6 pt-12 text-center">
        <v-btn class="mr-3" text @click="dialog = false">取消</v-btn>

        <v-btn color="success" text @click="callbackFun">确定</v-btn>
      </v-card-text>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  name: "DeleteHint",
  props: {
    icon: {
      type: String,
      required: true
    },
    text: {
      type: String,
      default: "确认删除么？"
    },
    func: {
      type: Function,
      required: true
    },
    tipText: {
      type: String,
      default: "删除"
    }
  },

  data: () => ({
    dialog: false,
    isShow: false
  }),

  methods: {
    callbackFun() {
      let result = this.func();
      if (result) {
        this.dialog = false;
      }
    }
  }
};
</script>